.light {
  --violet: #572ee5;
  --violet-white: #572ee5;
  --green: rgb(79, 227, 193);
  --grey-black: #e7e7e7;
  --grey: #e7e7e7;
  --black-white: #121212;
  --white-black: white;
}

.dark {
  --violet: #572ee5;
  --violet-white: #ffffff;
  --green: rgb(79, 227, 193);
  --grey-black: #212121;
  --grey: #e7e7e7;
  --black-white: #ffffff;
  --white-black: rgb(55, 55, 55);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  border-top: 5px solid var(--violet);
  background: var(--white-black);
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.principal-container {
  width: 900px;
  border: 1px solid var(--grey);
  font-weight: 700;
}

.violet-text {
  font-family: "Roboto", sans-serif;
  color: var(--violet-white);
}

.nav {
  margin: 7% 0;
}

.principal-title {
  font-size: 36px;
  line-height: 42px;
}

.container-searched {
  flex: 1 1;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.input-search {
  outline: none;
  font-size: 18px;
  border: none;
  background: none;
  width: 119%;
  height: 40%;
  color: var(--black-white);
  -webkit-transform: translateX(1px);
          transform: translateX(1px);
  border-radius: 61px;
  padding-left: 0px;
}
.btn-search {
  width: 86px;
  padding: 2.5%;
  background: var(--violet);
  border-left: 1px solid var(--black-white);
  border-radius: 0 61px 61px 0;
  -webkit-transform: translateX(0.5px);
          transform: translateX(0.5px);
  height: 49px;
  border-right: 0;
  border-top: 0;
  border-bottom: 0;
}
.first-input {
  padding-right: 25%;
  padding-top: 3%;
  padding-bottom: 3%;
  border-bottom: 1px solid var(--grey);
  border-radius: 0;
  display: flex;
  flex-direction: column;
  align-items: baseline;
}
.btn-search:hover {
  background: var(--green);
  cursor: pointer;
}
.btn-open {
  border-radius: 0 23px 0px 0;
}
.icon-search {
  width: 100%;
  height: 100%;
  background: none;
}
.search-open {
  border: 1px solid var(--black-white);
  border-radius: 25px;
  width: 542px;
  padding-left: 1.5%;
  margin-left: 1%;
}
.search-close {
  border: 1px solid var(--black-white);
  border-radius: 61px;
  border-right: none;
  width: 542px;
  padding-left: 2.5%;
}
.autocomplete-items {
  z-index: 9999;
  background: var(--white-black);
  padding: 2%;
}

.autocomplete {
  display: flex;
  flex-direction: column;
}
.no-autocomplete {
  border: none;
}

.autocomplete-items {
  z-index: 9999;
  background: var(--white-black);
}

.autocomplete-items:hover {
  background: var(--grey);
  cursor: pointer;
}

.cards-container {
  background: var(--grey-black);
  width: 100%;
  justify-content: space-around;
  margin: 2% 0;
  padding: 3% 0;
  flex-wrap: wrap;
}

.secundary-title {
  font-size: 24px;
  line-height: 28px;
  font-weight: 400;
}

.card {
  width: 250px;
  height: 250px;
  margin: 1.5% 0;
  border: 3px solid var(--white-black);
  border-radius: 5px;
}

.notification {
  font-size: 20px;
  border: 3px solid var(--green);
  border-radius: 15px;
  padding: 6%;
}

.header {
  padding: 1% 7%;
  align-items: center;
  justify-content: space-between;
}

.logo {
  width: 59.11px;
  height: 59.11px;
}

.btn {
  border: 1px solid var(--violet-white);
  background: var(--white-black);
  box-sizing: border-box;
  border-radius: 61px;
  padding: 1% 2%;
  font-weight: 700;
}

.btn:hover {
  cursor: pointer;
  color: var(--green);
}

.loading {
  width: 100%;
}

.img-loading {
  height: 100px;
}

